header{
    width: 100%;
    background-color:$bg-color;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 50;
    &::before{
        content:'';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
    }
    .header-wrp{
        min-height: 123px;     
        display: flex;
        justify-content: space-between;
        align-items: center;
        .logo{
            font-size: 40px;
            color: $primary-color;
            font-family:$Roboto-Condensed-Bold;  
            overflow: hidden;
            position: relative;
            // z-index: 3;     
        }
        .header-nav{
           height:100%;
            .nav-list{
                display: flex;
                justify-content: space-between;
                position: relative;
                z-index: 2;
                .nav-list__item{                    
                    & a{
                        font-size:16px;
                        font-family:$Roboto-Condensed-Bold;
                        color: $primary-color;
                        margin-left: 20px;
                        &:hover{
                            color:#1abb9c;
                        }
                    }
                }
            }
        }
    }
}


.header__burger{
    display: none;
}

@media screen and (max-width: 768px){
header{
    max-height: 103px;
}
    .header-wrp{
        max-height: 100px;
        justify-content: center !important;
    }
    // .logo{
       
    // }
    .nav-list{
        flex-direction: column;
        height: 100%;  
        align-items: center; 
    }
    .nav-list__item{

        & a{
            color: #fff !important;
            font-size: 25px;
        }
    }
    body.lock{
        overflow: hidden;
    }  
    .wrp-bg{
        margin-top: -20px;
        width: 100%;
        height: 50px;
        background-color: rgb(70, 66, 67);
    }
    .header__burger{
        display: block;
        position: relative;
        width: 30px;
        height: 20px;
        z-index: 3;
        left:90%;
        bottom: -17px;
    }
    .header__burger span{
        position: absolute;
        background-color: #fff;
        left: 0;
        width: 100%;
        height: 2px;
        top: 9px;
        transition: all .3s ease 0s;
    }
    .header__burger::before,
    .header__burger::after{
        content: '';
        background-color: #fff;
        position: absolute;
        width: 100%;
        height: 2px;
        left: 0;
        transition: all .3s ease 0s;
    }
    .header__burger::before{
        top: 0;
    }
    .header__burger::after{
        bottom: 0;
    }
    .header__burger.active::before{
        transform: rotate(45deg);
        top: 9px;
    }
    .header__burger.active::after{
        transform: rotate(-45deg);
        bottom: 9px;
    }
    .header__burger.active span{
        transform: scale(0);
    }
    .header-nav{
        position: fixed;
        top: 0;
        right: -100%;
        width: 50%;
        height: 100%;
        background-color: rgb(70, 66, 67);
        padding:120px 10px 20px 0px ;
        transition: all .3s ease 0s;
        overflow: auto;
    }
    .header-nav.active{
        right:  0;
    }
    .banner{
        margin-top: 100px;
    }
}
@media screen and (max-width: 426px){
    .header-nav{
        position: fixed;
        top: -100%;
        right: 0;
        width: 100%;
        height: 100%;
        background-color: rgb(70, 66, 67);
        padding:120px 10px 20px 0px ;
        transition: all .3s ease 0s;
        overflow: auto;
    }
    .header-nav.active{
        top:  0;
    }
}
