main{
    width: 100%;
}

        // SECTION 1

        
.banner{
    margin-top: 123px;
    width: 100%;
    min-height: 600px;
    background-image: url(../../../img/svg/Layer.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    .main-wrp-s1{
      display: flex;
      flex-direction: column; 
      .banner-title{
        font-size: 40px;
        color: #f3f3f3;
        font-family:$Roboto-Slab-Bold;
        line-height: 0.88;
        text-align: center;
        margin-top: 187px;
      }
      .banner-subtitle{
        color: #a8a8a8;
        font-size: 32px;
        font-family:$Roboto-Slab-Bold;
        line-height: 1.09;
        text-align: center;
        margin-top: 45px;
      }
      .banner-btn{
        margin-top: 45px;
        align-self: center;
        display: block;
        font-family: $Roboto-Regular;
        font-size: 14px;
        color: #fff;
        padding: 15px 13px;
        background-color: #3f8dbf;
        transition: all 0.3s;
        position: relative;
        border-radius:3px;
        &:hover{ 
          color: #3f8dbf;
          background-color: #fff;
          &:hover::before{
            content: '';
            position: absolute;
            width: 5px;
            height: 5px;
            background-color: #fff;
            border-radius: 50%;
            top: -10px;
            right: -10px;   
          }
        }
      }
    }
}


        // SECTION 2

.info__blocks{
  min-height: 333px;
  
}  
.main-wrp-s2{
  padding-top: 50px;
  position: relative;
  min-height: 333px;
  &::before{
    content: '';
    position: absolute;
    height: 2px;
    width: 100%;
    background-color: #000;
    bottom: 0;
    left: 0;
    opacity: .11;
  }
}
.info__blocks__list{
  display: flex;
  justify-content: space-around;
  height: 100%; 
  .info__list-item{
    width: 250px;
    padding:10px 10px;
    .info__list-item__img{
      width: 90px;
      height: 90px;
      background-color: #9d9abf;
      border-radius: 50%;
      margin: 0 auto;  
      display: flex;   
      justify-content: center;   
      align-items: center;
    }
    
    .info__list-item__title{
      text-align: center;
      font-family: $Roboto-Slab-Bold;
      font-size: 18px;
      color: $primary-color;
      margin-top: 24px;
    }
    .info__list-item__text{
      text-align: center;
      font-size: 14px;
      font-family:$Roboto-Regular;
      line-height: 1.71;
      margin-top: 12px;
    }
  }
  .info__list-item:nth-child(2) .info__list-item__img{
    background-color: #d2514b;
  }
  .info__list-item:nth-child(3) .info__list-item__img{
    background-color: #66a3c6;
  }
  .info__list-item:nth-child(4) .info__list-item__img{
    background-color: #e8c04f;
  }
}  


        // SECTION 3
.works-items{
  padding-top: 59px;
  min-height: 559px; 
}
.works-items__title{
  font-size: 18px;
  line-height: 1.94;
  color:$primary-color;
  font-family: $Roboto-Condensed-Bold;
}
.works-items__list{
  display: flex;
  justify-content: space-between;
  margin-bottom: 35px;
  margin-top: 20px;
  .works-items__list-item{
    max-width: 380px;
    margin-right: 15px;
    padding: 20px 0;
  
    .works-items__list-item-img{
      display: block;
      transition: all .3s;
      &:hover{
        transform: translateY(-10px);
      }
      & img{
        width: 100%;
        max-height: 270px;
      }
    }
    .works-items__list-item__subtitle{
      display: inline-block;
      font-size: 18px;
      margin-top: 20px;
      color:$primary-color;
      font-family:$Roboto-Slab-Bold;
      &:hover{
        color: #1abb9c;
      }
    }
    .works-items__list-item__subtext{
      margin-top: 6px;
      font-size: 13px;
      color: #a8a8a8;
      font-family:$Roboto-Slab-Regular;
    }
    .works-items__list-item__text{
      margin-top: 20px;
      font-size: 14px;
      color: #5c5c5c;
      font-family:$Roboto-Regular;
      line-height: 2;
    }
  }
}
.works-items__list li:last-child{
  margin-right: 0;
}
    // SECTION-4

.quote__block{
  width: 100%;
  background: rgba(0,0,0, .05);
}
.main-wrp-s4{
  min-height: 230px;
  display: flex;
  justify-content: center;
  position: relative;
  padding-bottom: 70px;
  .quote__block__text{ 
    margin-top: 112px;  
    text-align: center;
    max-width: 760px;
    font-size: 20px;
    color: #a1a1a1;
    font-family: $Roboto-Slab-Regular;
    line-height: 1.5;
    &::after{
      content: '”';
      position: absolute;
      color: #dcdcdc;
      font-size: 100px;
      top: 0;
      left: calc(50% - 17px);
      font-family: Arial;
    }
  }
}

   // SECTION-5

.feedback{
  width: 100%;
  min-height: 345px;
  background-color: $bg-color;
  padding-top: 49px;
}
.main-wrp-s5{
  display: flex;
  justify-content: space-between;
  .feedback-block{
    width: 261px;

    padding: 5px;
    .feedback-block__title a{
      font-size: 18px;
      color: #6c6c6c;
      line-height: 1.22;
      font-family: $Roboto-Condensed-Bold;
      &:hover{
        color: #1abb9c;
      }
    }
    .feedback-block__text{
      margin-top: 15px;
      font-size: 14px;
      line-height: 1.71;
      color: #cacaca;
      font-family:$Roboto-Regular;
      
    }
    .feedback-block__list{
      margin-top: 20px;
      display: flex;
      flex-direction: column;
      .feedback-block__list-item{
        margin-top: 10px;
        & a{
          color: #cacaca;
          font-size: 14px;
          font-family:$Roboto-Regular;
          line-height: 1.5;
          display: inline-flex;
          &:hover{
            text-decoration: underline;
          }
          
        }
      }
    }
  }
}
.icons-social{
  color: #a89574;
  font-size: 14px;
  height: 50%;
  margin-right: 10px;
}












        // @MEDIA

@media screen and (max-width: 960px) {
  .wrapper{
    padding-right: 20px;
    padding-left: 20px;
  }
  
}
@media screen and (max-width: 769px){

  .banner{
    min-height: 300px;  
    .main-wrp-s1{
      
      .banner-title{
        font-size: 25px;
        margin-top: 120px;
      }   
      .banner-subtitle{
        font-size: 20px;
        margin-top: 20px;
      } 
      .banner-btn{
        margin-top: 25px; 
        margin-bottom: 40px;
      }       
    }            
  } 
  .main-wrp-s2{
    padding-bottom: 40px;
  }
  .info__blocks__list{
    display: flex;
    justify-content: space-around;
    height: 100%; 
    flex-wrap: wrap;  
    padding: 0 55px;   
  }  
  .works-items__title{
   text-align: center;
  }
  .works-items__list{
    display: flex;
    justify-content: space-between;
    margin-bottom: 35px;
    margin-top: 20px;
    flex-direction: column;
    align-items: center;
    .works-items__list-item{
      margin-right: 0;

    }
  } 
  .main-wrp-s4{
    .quote__block__text{ 
      margin-top: 75px; 
    }
  } 
.feedback{
  background-color: rgba(0,0,0, .9);
  .feedback-block{
    width: 80%;
  }
}
  .main-wrp-s5{
    flex-direction: column;
    align-items: center;
    padding-bottom: 50px;

    .feedback-block{
      margin-top:25px ;
    }
  }
  .footer{
    background-color: rgba(0,0,0, .9);
  }

}
@media screen and (max-width: 426px) {
  .banner{

    .main-wrp-s1{

      .banner-title{
        line-height: normal;
      }
      .banner-subtitle{
        display: none;
      }  
      .banner-btn{
        margin-bottom: 0;
      }       
    }
  }

  .info__blocks__list{
    
    .info__list-item{
      width: 350px;
      margin-top: 25px;
    }
  }
  .works-items__list{   
    .works-items__list-item{
      max-width: 300px;
    }
  }
  .works-items__list-item__text{
    width: 90%;
  }
}
